/* Projects 컴포넌트 스타일 */
.projects {
  padding: 80px 20px;
  background: #f8f9fa;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.projects-top {
  width: 100%;
  max-width: 1200px;
  display: flex;
  justify-content: flex-end;
  margin-bottom: 2rem;
}

.page-ctrl {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.nav-btn {
  background: #007bff;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.nav-btn:hover:not(:disabled) {
  background: #0056b3;
}

.nav-btn:disabled {
  background: #ccc;
  cursor: not-allowed;
}

.page-label {
  font-weight: bold;
  color: #333;
}

.pills {
  display: flex;
  gap: 1rem;
  margin-bottom: 3rem;
}

.pill {
  background: white;
  border: 2px solid #ddd;
  padding: 0.5rem 1.5rem;
  border-radius: 25px;
  cursor: pointer;
  transition: all 0.3s;
  font-weight: bold;
}

.pill:hover {
  border-color: #007bff;
}

.pill.is-active {
  background: #007bff;
  color: white;
  border-color: #007bff;
}

.project-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  max-width: 1200px;
  width: 100%;
}

.project-card {
  background: white;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s;
}

.project-card:hover {
  transform: translateY(-5px);
}

.thumb {
  width: 100%;
  height: 200px;
  border: none;
  background: #f0f0f0;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.thumb img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.ph {
  width: 100%;
  height: 100%;
  background: #e0e0e0;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #999;
  font-size: 1.2rem;
}

.card-title {
  font-size: 1.3rem;
  font-weight: bold;
  margin: 1rem 1rem 0.5rem;
  color: #333;
}

.card-desc {
  margin: 0 1rem 1rem;
  color: #666;
  line-height: 1.5;
}

.tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin: 0 1rem 1rem;
}

.tag {
  background: #e9ecef;
  color: #495057;
  padding: 0.25rem 0.5rem;
  border-radius: 15px;
  font-size: 0.8rem;
}

.pj-scrim {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1000;
  border: none;
  cursor: pointer;
}

.pj-modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: white;
  border-radius: 10px;
  max-width: 800px;
  width: 90%;
  max-height: 80vh;
  overflow-y: auto;
  z-index: 1001;
}

.pj-close {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: none;
  border: none;
  font-size: 2rem;
  cursor: pointer;
  color: #666;
}

.pj-body {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
  padding: 2rem;
}

.pj-media {
  display: flex;
  align-items: center;
  justify-content: center;
}

.pj-media img {
  width: 100%;
  height: auto;
  border-radius: 5px;
}

.pj-title {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 1rem;
  color: #333;
}

.pj-detail {
  color: #666;
  line-height: 1.6;
  margin-bottom: 1rem;
}

@media (max-width: 768px) {
  .pj-body {
    grid-template-columns: 1fr;
  }
  
  .project-grid {
    grid-template-columns: 1fr;
  }
}
/* =========================
   Shadows for project slider
   ========================= */

/* 공용 쉐도우 토큰 */
:root{
  --pj-shadow-sm: 0 6px 16px rgba(0,0,0,.10), 0 2px 6px rgba(0,0,0,.06);
  --pj-shadow-md: 0 10px 24px rgba(0,0,0,.14), 0 4px 10px rgba(0,0,0,.08);
  --pj-shadow-lg: 0 18px 42px rgba(0,0,0,.18), 0 8px 20px rgba(0,0,0,.10);
}

/* 뷰포트 좌우 페이드(끝쪽 깊이감) */
.project-viewport{
  position: relative;
  isolation: isolate;
}
.project-viewport::before,
.project-viewport::after{
  content:"";
  position:absolute; top:0; bottom:0; width:36px;
  pointer-events:none; z-index:2;
  /* 좌/우 끝 살짝 그라데이션 */
  background: linear-gradient(to right, rgba(0,0,0,.06), rgba(0,0,0,0));
}
.project-viewport::before{ left:0; }
.project-viewport::after{
  right:0;
  transform: scaleX(-1); /* 같은 그라데이션 반전 */
}

/* 슬라이드 자체에 아주 약한 바닥 음영 */
.project-slide{
  position: relative;
}
.project-slide::after{
  content:"";
  position:absolute; left:0; right:0; bottom:-2px; height:2px;
  background: radial-gradient(100% 40% at 50% 0, rgba(0,0,0,.14), rgba(0,0,0,0));
  pointer-events:none;
}

/* 카드: 배경 + 쉐도우 + 호버 상승 */
.project-card{
  background:#fff;
  border-radius: 14px;
  box-shadow: var(--pj-shadow-sm);
  padding: 10px;                /* 카드의 여백 */
  transition: transform .28s cubic-bezier(.2,.9,.22,1), box-shadow .28s ease;
  will-change: transform;
}
.project-card:hover{
  transform: translateY(-4px);
  box-shadow: var(--pj-shadow-md);
}
.project-card:active{
  transform: translateY(-1px);
  box-shadow: var(--pj-shadow-sm);
}

/* 썸네일도 살짝 떠 보이게 */
.thumb{
  box-shadow: 0 6px 16px rgba(0,0,0,.10);
  transition: box-shadow .28s ease, transform .28s cubic-bezier(.2,.9,.22,1);
  transform: translateZ(0);
}
.project-card:hover .thumb{
  box-shadow: var(--pj-shadow-lg);
  transform: translateY(-2px);
}

/* 포커스 접근성 (키보드 탐색 시) */
.thumb:focus-visible{
  outline: 3px solid #00EAF5;
  outline-offset: 2px;
}

/* 모션 줄이기 */
@media (prefers-reduced-motion: reduce){
  .project-card,
  .thumb{
    transition: none;
  }
}
/* ==== No-gray overrides ==== */

/* 슬라이더 좌우 페이드 제거 */
.project-viewport::before,
.project-viewport::after{
  content: none !important;
}

/* 슬라이드 바닥 그라데이션 제거 */
.project-slide::after{
  content: none !important;
}

/* 썸네일/카드 배경은 투명 or 흰색으로 깔끔하게 */
.thumb{ background: transparent !important; }
.project-card{ background: #fff; }

/* 섹션 자체 배경도 투명 유지 (혹시 모를 누적 스타일 방지) */
.projects{ background: transparent; }

:root{
  --pj-max-w: min(1200px, 92vw);
  --pj-cyan: #00EAF5;
  --pj-ink: #0b0c0d;
  --pj-muted: #6b7280;
  --pj-yellow: #FFFD6B;

  --pj-slide-ms: 520ms;
  --pj-ease: cubic-bezier(.2,.9,.22,1);

  /* 카드 간격 토큰 */
  --pj-gap: clamp(18px, 3vw, 28px);
}

/* ====== 슬라이더 레이아웃은 그대로 ====== */
.project-viewport{ overflow: hidden; width:100%; position:relative; }
.project-slider{ display:flex; transition: transform var(--pj-slide-ms) var(--pj-ease); will-change:transform; }
.project-slide{ min-width:100%; padding: 2px 0; box-sizing:border-box; }

/* ====== 그리드 → flex 중앙 정렬 ====== */
.project-grid{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;  /* ← 항상 가운데 정렬 */
  gap: var(--pj-gap);
}

/* 데스크톱: 4열(=슬라이드당 4개이므로 한 줄에 딱 맞음) */
.project-card{
  flex: 1 1 calc(25% - var(--pj-gap));
  max-width: calc(25% - var(--pj-gap));
  background:#fff; border-radius: 14px;
  box-shadow: 0 6px 16px rgba(0,0,0,.10), 0 2px 6px rgba(0,0,0,.06);
  padding: 10px;
  transition: transform .28s cubic-bezier(.2,.9,.22,1), box-shadow .28s ease;
}
.project-card:hover{
  transform: translateY(-4px);
  box-shadow: 0 10px 24px rgba(0,0,0,.14), 0 4px 10px rgba(0,0,0,.08);
}

/* 태블릿: 2열 */
@media (max-width: 960px){
  .project-card{
    flex: 1 1 calc(50% - var(--pj-gap));
    max-width: calc(50% - var(--pj-gap));
  }
}

/* 모바일: 1열 */
@media (max-width: 640px){
  .project-card{
    flex: 1 1 100%;
    max-width: 100%;
  }
}

/* 썸네일/텍스트 등 기존 스타일 유지 */
.thumb{
  width: 100%; aspect-ratio: 16/10;
  border-radius: 12px; border: none; padding: 0;
  overflow: hidden; background: transparent; cursor: pointer; display: block;
  box-shadow: 0 6px 16px rgba(0,0,0,.10);
}
.thumb img{ width:100%; height:100%; object-fit:cover; display:block; }
.card-title{ margin: 12px 0 4px; font-size: clamp(18px, 2.6vw, 22px); font-weight: 800; }
.card-desc{ margin: 0 0 10px; color: var(--pj-muted); font-size: 14px; }
.tags{ display: flex; flex-wrap: wrap; gap: 10px; }
.tag{ color: #00cbd6; font-size: 13px; }
/* 슬라이더 트랙/슬라이드 가운데 정렬 보정 */
.project-viewport{
  overflow: hidden;
  width: 100%;
  position: relative;

  /* 뷰포트 자체도 가운데 기준 */
  display: flex;
  justify-content: center;
}

.project-slider{
  display: flex;
  width: 100%;
  /* translateX로 페이지 이동해도 가운데 기준 유지 */
  transform: translateX(var(--x, 0));
}

.project-slide{
  /* 한 페이지 = 뷰포트 100% 폭 */
  min-width: 100%;
  box-sizing: border-box;

  /* 슬라이드 내부 컨텐츠를 수평 중앙에 */
  display: flex;
  justify-content: center;
}

/* 슬라이드 내부 카드 컨테이너(그리드)를 가운데 고정 */
.project-grid{
  width: 100%;
  max-width: var(--pj-max-w);   /* 프로젝트 섹션과 같은 최대폭 */
  margin: 0 auto;               /* 항상 중앙 */
  display: flex;
  flex-wrap: wrap;
  justify-content: center;      /* 요소 수가 모자라도 중앙 유지 */
  gap: var(--pj-gap, clamp(18px, 3vw, 28px));
}

/* 카드 폭: 데스크톱 4개, 태블릿 2개, 모바일 1개 */
.project-card{
  flex: 1 1 calc(25% - var(--pj-gap, 24px));
  max-width: calc(25% - var(--pj-gap, 24px));
}
@media (max-width: 960px){
  .project-card{
    flex: 1 1 calc(50% - var(--pj-gap, 24px));
    max-width: calc(50% - var(--pj-gap, 24px));
  }
}
@media (max-width: 640px){
  .project-card{
    flex: 1 1 100%;
    max-width: 100%;
  }
}
