@import url("https://fonts.googleapis.com/css2?family=Krona+One&display=swap");

/* =========================
   전역/페이지 스크롤
   ========================= */
:root{
  --topbar-h: 64px;
  --page-pad: clamp(16px, 4vw, 24px);

  /* Hero */
  --ellipse-w: min(1100px, 94vw);
  --ellipse-ratio: 2.55 / 1;
  --ellipse-white-top-alpha: .98;
  --ellipse-c1: #00F6FF;
  --ellipse-c2: #00F6FF;

  --title-letterspace: 0.045em;
  --title-stroke-width: 1.6px;
  --title-stroke-color: rgba(0,0,0,.95);
  --title-shadow-1: rgba(0,0,0,.24);
  --title-shadow-2: rgba(0,0,0,.16);
  --title-shadow-3: rgba(0,0,0,.12);
  --badge-scale: 1.6;

  /* 메뉴 */
  --menu-bg: #FFFD6B;
  --menu-accent: #0ea5e9;
  --menu-w: clamp(360px, 35vw, 720px);
  --menu-radius: 16px;
  --menu-font: "Krona One", system-ui, -apple-system, Segoe UI, Roboto, "Noto Sans KR", Arial, sans-serif;
  --menu-item-size: clamp(26px, 4.4vw, 44px);
  --menu-gap: clamp(14px, 2.4vw, 26px);

  /* 섹션 */
  --section-pad: clamp(16px, 3vw, 40px);
}

*{ box-sizing: border-box; }
html{ scroll-behavior: smooth; }

/* 섹션 공통 */
.section{
  min-height: 100svh;
  display: grid;
  place-items: center;
  padding: var(--section-pad);
  scroll-snap-align: start;
  scroll-margin-top: var(--topbar-h);
}

/* contact 섹션 스크롤 스냅 조정 */
.section--contact{
  scroll-snap-align: start;
  padding-bottom: max(var(--section-pad), 40px);
}

/* 바닥 버퍼 1px: 끝점 들러붙음 방지 */
#pageRoot::after{
  content:"";
  display:block;
  height: 1px;
}

body{
  margin: 0;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, "Noto Sans KR", Arial, sans-serif;
  color: #0b0c0d;
  background: #fff;
}

/* =========================
   히어로(홈)
   ========================= */
.hero{ position:relative; background:#fff; }
.intro-scrim{position:fixed; inset:0; background:#fff; z-index:9; opacity:1;}
.intro-scrim.hide{animation:scrimOut .6s ease forwards;}
@keyframes scrimOut{to{opacity:0; visibility:hidden}}

.hero-center{
  width:100%; min-height: calc(100svh - var(--topbar-h));
  display:grid; place-items:center;
}

.ellipse{
  position: relative;
  width:var(--ellipse-w);
  aspect-ratio: var(--ellipse-ratio);
  clip-path: ellipse(50% 50% at 50% 50%);
  background:
    radial-gradient(
      ellipse 95% 150% at 50% 42%,
      var(--ellipse-c1) 0%,
      var(--ellipse-c2) 60%,
      #c9fbff 95%,
      rgba(255,255,255,0) 100%
    );
  background-repeat:no-repeat;
  background-size:100% 100%;
  box-shadow: 0 24px 60px rgba(0,0,0,.10), 0 8px 24px rgba(0,0,0,.08);
  display:flex; align-items:center; justify-content:center;
  padding:clamp(14px, 2.8vw, 28px);
  opacity:0; transform:translateY(20px) scale(.95);
  z-index:0;
}
.ellipse::before{
  content:"";
  position:absolute; inset:0;
  clip-path:inherit; pointer-events:none; z-index:0;
  background: linear-gradient(
    to top,
    rgba(255,255,255,0) 10%,
    rgba(255,255,255,.25) 20%,
    rgba(255,255,255,.55) 55%,
    rgba(255,255,255,var(--ellipse-white-top-alpha)) 100%
  );
}

.ellipse-inner{
  position: relative; z-index: 1;
  width:100%; max-width:84%;
  text-align:center;
  display:flex; flex-direction:column; align-items:center;
  font-size: clamp(14px, 2.0vw, 22px);
  gap: .19em;
}

.badge{
  margin-top: clamp(0.8em, 1.6vw, 1.3em);
  padding: calc(.10em * var(--badge-scale)) calc(0.3em * var(--badge-scale));
  border: 1px solid #0b0c0d; border-radius: 999px;
  background: transparent; color: #0b0c0d;
  font-size: calc(1em * var(--badge-scale));
  letter-spacing: .02em; opacity:0; transform: translateY(26px);
}

.title{
  margin:.10em 0 .25em;
  font-size: 4.2em; line-height:1.08;
  letter-spacing: var(--title-letterspace);
  color:#0b0c0d; text-align:center;
  position: relative; z-index: 1;
  font-family: var(--menu-font);
  font-weight: 400; font-synthesis: none;

  -webkit-text-stroke: var(--title-stroke-width) var(--title-stroke-color);
  text-shadow:
    0 18px 36px var(--title-shadow-1),
    0 10px 22px var(--title-shadow-2),
    0 4px 10px  var(--title-shadow-3);

  opacity:0; transform:translateY(38px);
}

.subtitle{
  margin:.1em 0; font-size:1.15em; color:#222; text-align:center;
  opacity:0; transform:translateY(40px);
}

@keyframes fadeUp{from{opacity:0; transform:translateY(40px)} to{opacity:1; transform:translateY(0)}}
@keyframes fadeUpSm{from{opacity:0; transform:translateY(8px)} to{opacity:1; transform:translateY(0)}}
@keyframes riseScale{from{opacity:0; transform:translateY(20px) scale(.95)} to{opacity:1; transform:translateY(0) scale(1)}}

.is-ready .ellipse{          animation:riseScale .8s cubic-bezier(.2,.9,.22,1) .05s forwards; }
.is-ready .ellipse .title{   animation:fadeUp   .9s cubic-bezier(.2,.9,.22,1) .25s forwards; }
.is-ready .ellipse .subtitle:nth-of-type(1){ animation:fadeUp .8s ease .45s forwards; }
.is-ready .ellipse .subtitle:nth-of-type(2){ animation:fadeUp .8s ease .60s forwards; }
.is-ready .ellipse .badge{   animation:fadeUpSm .6s ease .80s forwards; }

/* =========================
   상단 고정 TopBar
   ========================= */
.tb{
  position: fixed;
  top: 0; left: 0; right: 0;
  height: var(--topbar-h);
  padding: 16px var(--page-pad);
  display: flex; justify-content: space-between; align-items: center;
  background: transparent; z-index: 9999;
  pointer-events: none;
}
.tb a, .tb button{ pointer-events: auto; }

.tb-links{ display:flex; gap: 18px; }

.tb-link{
  position: relative; display: inline-block;
  color: #111; text-decoration: none; line-height: 1; cursor: pointer;
}
.tb-label{
  position: relative; z-index: 1; padding: 6px 6px; border-radius: 8px;
  transition: color .25s ease;
  font-family: var(--menu-font); font-weight: 400; letter-spacing: .02em;
}
.tb-link::after{
  content: ""; position: absolute; left: 0; bottom: -3px;
  width: 100%; height: 2px; background: #111; border-radius: 2px;
  transition: height .28s cubic-bezier(.2,.9,.22,1),
              bottom .28s cubic-bezier(.2,.9,.22,1),
              border-radius .28s cubic-bezier(.2,.9,.22,1),
              background .28s ease;
  z-index: 0;
}
.tb-link:hover::after{ bottom: 0; height: 100%; border-radius: 8px; background: #111; }
.tb-link:hover .tb-label{ color: #fff; }

.tb-hamburger{
  width: 36px; height: 28px; display: grid; gap: 5px;
  background: transparent; border: 0; cursor: pointer; border-radius: 8px;
}
.tb-hamburger span{
  height: 2px; background: #111; border-radius: 2px; display: block;
  transform: scaleY(1); transform-origin: center;
  transition: transform .22s cubic-bezier(.2,.9,.22,1);
}
.tb-hamburger:hover span{ transform: scaleY(1.7); }

/* 좌측 페이지명 스타일 */
.tb-title{
  font-family: var(--menu-font);
  font-weight: 400;
  letter-spacing: var(--title-letterspace);
  font-size: clamp(22px, 3.4vw, 36px);
  line-height: 1;
  color: #0b0c0d;
  -webkit-text-stroke: 1px rgba(0,0,0,.85);
  text-shadow:
    0 10px 20px var(--title-shadow-1),
    0 6px 14px  var(--title-shadow-2),
    0 3px 6px   var(--title-shadow-3);
  display: flex;
  align-items: center;
  height: 100%;
  padding: 0 4px;
  pointer-events: none;
}

/* =========================
   슬라이드 메뉴
   ========================= */
.tb-menu{
  position: fixed;
  top: 12px; right: 12px;
  width: var(--menu-w);
  height: calc(100svh - 24px);
  background: var(--menu-bg);
  border-radius: var(--menu-radius);
  transform: translateX(110%);
  transition: transform .42s cubic-bezier(.2,.9,.22,1);
  z-index: 30;
  padding: 18px;
  box-shadow: 0 24px 48px rgba(0,0,0,.25), -8px 0 24px rgba(0,0,0,.10);
}
.tb-menu.is-open{ transform: translateX(0); }

.tb-menu-close{
  align-self: flex-start;
  width: 52px; height: 52px;
  border: none; background: transparent; cursor: pointer;
  font-size: 40px; line-height: 1; color: #222;
  border-radius: 12px;
  position: relative; z-index: 2;
}
.tb-menu-close:hover{ background: rgba(0,0,0,.06); }

.tb-menu-nav{
  position: absolute; inset: 0;
  display: grid; place-content: center; justify-items: center;
  gap: var(--menu-gap); text-align: center; padding: 32px; z-index: 1;
}
.tb-item{
  position: relative; display: block; text-decoration: none; color: #111;
  font-family: var(--menu-font); font-weight: 400; letter-spacing: .02em;
  font-size: var(--menu-item-size); line-height: 1.15;
  padding: 8px 12px; border-radius: 10px; overflow: hidden;
  opacity: 0; transform: translateX(24px);
  animation: tbIn .45s cubic-bezier(.2,.9,.22,1) forwards;
}
.tb-item:nth-child(1){ animation-delay: .08s; }
.tb-item:nth-child(2){ animation-delay: .16s; }
.tb-item:nth-child(3){ animation-delay: .24s; }
.tb-item:nth-child(4){ animation-delay: .32s; }

@keyframes tbIn { to { opacity: 1; transform: translateX(0); } }

.tb-item .label,
.tb-item .label-hover{
  display: block;
  transition: transform .26s ease, opacity .26s ease, color .26s ease;
  font-family: var(--menu-font); font-weight: 400;
}
.tb-item .label-hover{
  position: absolute; inset: 0; display: flex; align-items: center; justify-content: center;
  opacity: 0; color: var(--menu-accent); transform: translateY(6px);
}
.tb-item:hover{ color: var(--menu-accent); }
.tb-item:hover .label{ opacity: 0; transform: translateY(-6px); }
.tb-item:hover .label-hover{ opacity: 1; transform: translateY(0); }

/* 스냅 잠깐 끄기 */
html.no-snap { scroll-snap-type: none !important; }

/* 모션 줄이기 */
@media (prefers-reduced-motion: reduce){
  html{ scroll-behavior: auto; }
  .intro-scrim.hide,
  .is-ready .ellipse,
  .is-ready .ellipse .title,
  .is-ready .ellipse .subtitle,
  .is-ready .ellipse .badge,
  .tb-menu{ animation:none; transition:none; opacity:1; transform:none; }
}
