:root{
  --ft-max-w: min(1200px, 92vw);
  --ft-ink: #0b0c0d;
  --ft-muted: #6b7280;
  --ft-border: rgba(0,0,0,.12);
  --ft-pad-y: clamp(16px, 2.6vw, 28px);
}

.footer{
  border-top: 1px solid var(--ft-border);
  background: #fff;
  color: #0b0c0d;
}

.footer-inner{
  width: 100%;
  max-width: var(--ft-max-w);
  margin: 0 auto;
  padding: var(--ft-pad-y) clamp(16px, 4vw, 24px);
  display: grid;
  grid-template-columns: 1fr auto;
  align-items: center;
  gap: 16px;
}

.f-brand{
  font-family: var(--menu-font, system-ui);
  letter-spacing: .02em;
  font-weight: 700;
}
.f-copy{
  margin-top: 4px;
  color: var(--ft-muted);
  font-size: 13px;
}

.f-right{
  display: inline-flex;
  align-items: center;
  gap: clamp(10px, 2vw, 16px);
  flex-wrap: wrap;
}
.f-link{
  text-decoration: none;
  color: var(--ft-ink);
  border: 1px solid #0b0c0d;
  border-radius: 999px;
  padding: 8px 12px;
  font-size: 13px;
  font-weight: 700;
}
.f-link:hover{ background: #0b0c0d; color: #fff; }

.f-link.to-top{ border-color: #00EAF5; }
.f-link.to-top:hover{ background: #00EAF5; color: #0b0c0d; }

/* 모바일 */
@media (max-width: 640px){
  .footer-inner{
    grid-template-columns: 1fr;
    justify-items: flex-start;
  }
}
