@import url("https://fonts.googleapis.com/css2?family=Krona+One&display=swap");

/* ===== 토큰 ===== */
:root{
  --about-max-w: min(1400px, 92vw);
  --about-gap: clamp(24px, 4vw, 64px);

  /* ⬇️ 좌측(이모지) 폭을 줄였어요 */
  --about-left-w: clamp(240px, 28vw, 380px);

  /* ⬇️ 패널 최소 높이(이 값에 맞춰 이모지도 자동으로 제한됨) */
  --panel-min-h: clamp(220px, 32vh, 360px);
  --panel-bg: #f3f4f6;

  --blue-1: #00C8FF;
  --blue-2: #008CFF;
  --yellow: #FFFD6B;
  --ink: #0b0c0d;

  --menu-font: "Krona One", system-ui, -apple-system, Segoe UI, Roboto, "Noto Sans KR", Arial, sans-serif;
}

/* ===== 컨테이너 ===== */
.about{
  width: 100%;
  max-width: var(--about-max-w);
  margin: 0 auto;
  padding-inline: clamp(8px, 2vw, 18px);
}
.about-wrap{ display: grid; }
.about-body{
  margin-top: 8px;
  display: grid;
  grid-template-columns: var(--about-left-w) 1fr;
  gap: var(--about-gap);
  align-items: start;
}

/* ===== 왼쪽 이모지(배경 투명, 우측 높이에 맞춰 사이즈 제한) ===== */
.about-avatar{
  display: grid;
  place-items: center;
  padding: 0;
  min-height: 0;
  background: transparent;   /* ✅ 투명 */
  border: none;              /* ✅ 테두리/채움 제거 */
  align-self: start;         /* 우측 상단과 맞춤 */
}
.avatar-img{
  width: clamp(160px, 18vw, min(320px, calc(var(--panel-min-h) - 40px)));
  height: clamp(160px, 18vw, min(320px, calc(var(--panel-min-h) - 40px)));
  object-fit: cover;
  border-radius: 18px;
  display: block;
  max-width: 100%;
}
.avatar-emoji{
  display: grid;
  place-items: center;
  line-height: 1;
  /* 패널 높이 기준으로 자동 제한 + 뷰포트에 따른 반응형 */
  font-size: clamp(120px, 18vw, calc(var(--panel-min-h) - 40px));
}

/* ===== 오른쪽 ===== */
.about-right{
  display: grid;
  grid-template-columns: 1fr;
  gap: clamp(16px, 2.2vw, 24px);
}

/* 파란 캡슐 탭 */
.seg{
  position: relative;
  display: grid;
  grid-template-columns: repeat(var(--cols, 3), 1fr);
  align-items: center;
  height: 68px;
  padding: 8px;
  border-radius: 9999px;
  background: linear-gradient(135deg, var(--blue-1), var(--blue-2));
  box-shadow: 0 14px 28px rgba(0,0,0,.10), 0 6px 16px rgba(0,0,0,.08);
}
.seg-btn{
  position: relative;
  z-index: 2; /* 텍스트가 노란 캡슐 위로 */
  height: 100%;
  border: 0;
  background: transparent;
  color: #fff;
  font-family: var(--menu-font);
  font-weight: 400;
  letter-spacing: .02em;
  font-size: clamp(14px, 1.2vw, 18px);
  cursor: pointer;
}
.seg-btn.is-active{ color: var(--ink); }
.seg-knob{
  position: absolute;
  top: 8px; left: 8px;
  height: calc(100% - 16px);
  width: calc((100% - 16px) / var(--cols, 3));
  background: var(--yellow);
  border-radius: 9999px;
  box-shadow: 0 10px 20px rgba(0,0,0,.12);
  transform: translateX(calc(var(--i, 0) * 100%));
  transition: transform .35s cubic-bezier(.2,.9,.22,1);
  z-index: 1;
}

/* ===== 패널: 밝은 회색 배경 / 테두리 없음 ===== */
.about-panel{
  background: var(--panel-bg);
  border: none;
  border-radius: 20px;
  padding: clamp(18px, 2.2vw, 24px);
  min-height: var(--panel-min-h);
  display: grid; grid-template-columns: 1fr; align-content: start; gap: 12px;
}

/* 공통 텍스트 */
.bio-title{ margin: 0 0 6px; font-size: clamp(16px, 2vw, 18px); font-weight: 800; }
.bio-text{ margin: 0; color: #222; font-size: 14px; line-height: 1.6; }

/* ===== EXPERIENCE ===== */
.exp-panel{ display: grid; gap: 6px; }
.experience-list{
  margin: 0; padding: 0; list-style: none;
  display: grid; gap: 0;
}
.exp-row{
  display: grid;
  grid-template-columns: 160px 180px 1fr; /* 날짜 / 타이틀 / 설명 */
  column-gap: 24px;
  align-items: start;
  padding: 18px 8px;
  border-bottom: 1px solid #e6e7eb;
}
.exp-row:last-child{ border-bottom: 0; }
.exp-date{ font-size: 12px; color: #6b7280; }
.exp-main{ display: grid; gap: 4px; }
.exp-main strong{ font-size: 14px; color: #0f172a; }
.exp-main small{ font-size: 11px; color: #94a3b8; }
.exp-side{ font-size: 13px; color: #334155; }

/* ===== SKILL ===== */
.skill-panel{ display: grid; gap: 10px; }
.skills-grid{
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  gap: 10px;
}
.skill-chip{
  display: inline-grid; place-items: center;
  padding: 8px 10px; border: 1.2px solid #000; border-radius: 999px; font-size: 12px; background: #fff;
}

/* 링크 칩 */
.about-links-row{
  display: grid; grid-auto-flow: column; justify-content: center; align-items: center;
  gap: 12px; padding-top: 4px;
}
.alink{
  display: inline-grid; grid-auto-flow: column; align-items: center; gap: 8px;
  height: 40px; padding: 0 14px; border: 1.2px solid #000; border-radius: 999px; background: #fff;
  color: inherit; text-decoration: none;
}
.alink-ico{ width: 18px; height: 18px; }
.alink-text{ font-size: 14px; }
.alink-muted{ color: #444; }

/* 반응형 */
@media (max-width: 980px){
  .about-body{ grid-template-columns: 1fr; }
  .avatar-img{
    width: clamp(160px, 48vw, calc(var(--panel-min-h) - 40px));
    height: clamp(160px, 48vw, calc(var(--panel-min-h) - 40px));
  }
  .avatar-emoji{
    font-size: clamp(120px, 40vw, calc(var(--panel-min-h) - 40px));
  }
  .about-links-row{ grid-auto-flow: row; justify-content: center; }
}
