:root{
  --ct-max-w: min(1200px, 92vw);
  --ct-ink: #0b0c0d;
  --ct-muted: #6b7280;
  --ct-cyan: #00EAF5;
  --ct-border: #000;
}

.contact{
  width: 100%;
  max-width: var(--ct-max-w);
  margin: 0 auto;
  transform: translateY(clamp(8px, 2.4vh, 36px));
}

/* ===== 왼쪽(폼) ===== */
.ct-title{ margin: 0 0 8px; font-size: clamp(20px, 3vw, 28px); font-weight: 800; }
.ct-desc{ margin: 0 0 18px; color: var(--ct-muted); font-size: 14px; }

.ct-form{ display: grid; gap: 12px; }
.field-row{ display: grid; grid-template-columns: 1fr 1fr; gap: 12px; }
.field{ display: grid; gap: 8px; }
.field label{ font-size: 12px; letter-spacing: .04em; color: var(--ct-muted); }
.field input, .field textarea{
  width: 100%; border: 1.2px solid var(--ct-border);
  border-radius: 12px; padding: 12px 14px; font-size: 14px; outline: none; background: #fff;
}
.field textarea{ min-height: 220px; resize: vertical; }
.field input:focus, .field textarea:focus{
  border-color: #111; box-shadow: 0 0 0 3px rgba(0, 234, 245, .25);
}
.submit-btn{
  margin-top: 8px; width: max-content; height: 44px; padding: 0 22px;
  border-radius: 999px; border: none; background: var(--ct-cyan);
  color: var(--ct-ink); font-weight: 800; letter-spacing: .04em; cursor: pointer;
}

/* ===== 오른쪽(Info) ===== */
.aside-title{ margin: 4px 0 12px; font-weight: 700; }
.info-list{ list-style: none; margin: 0 0 12px; padding: 0; display: grid; gap: 12px; }
.info-item{ display: grid; grid-template-columns: 24px 1fr; align-items: start; gap: 10px; }
.info-ico{ width: 20px; height: 20px; }
.info-text{ display: grid; gap: 2px; }
.info-label{ font-size: 11px; letter-spacing: .04em; color: var(--ct-muted); }
.info-link{ font-size: 13px; color: inherit; text-decoration: none; word-break: break-all; }

/* ===== 캘린더 ===== */
.cal{ width: 100%; border: 1.2px solid #000; border-radius: 12px; padding: 10px; box-sizing: border-box; overflow: hidden; position: relative; }
.cal *{ min-width: 0; }

.cal-head{
  display: grid; grid-template-columns: 28px 1fr 28px; align-items: center; gap: 6px; margin-bottom: 6px;
}
.cal-title-btn{
  text-align: center; font-weight: 800; font-size: 13px;
  border: 1.2px solid #000; background: #fff; border-radius: 8px; height: 28px; padding: 0 10px; cursor: pointer;
}
.cal-nav{
  width: 28px; height: 28px; display: grid; place-items: center;
  border-radius: 8px; border: 1.2px solid #000; background: #fff; cursor: pointer; padding: 0;
}

.cal-grid{ display: grid; grid-template-columns: repeat(7, 1fr); gap: 4px; margin-bottom: 8px; }
.cal-wk{ text-align: center; font-size: 10px; color: #000; font-weight: 700; }
.cal-cell{
  position: relative; aspect-ratio: 1 / 1; border: 1.1px solid #000; border-radius: 8px; background: #fff; cursor: pointer;
}
.cal-cell.is-today{ outline: 2px solid #00EAF5; outline-offset: 0; }
.cal-cell.is-selected{ background: #f4f4f4; }
.cal-empty{ background: #fafafa; border-style: dashed; }
.cal-day{ position: absolute; top: 5px; right: 6px; font-size: 11px; font-weight: 700; }
.cal-dot{ position: absolute; bottom: 5px; left: 50%; transform: translateX(-50%); width: 5px; height: 5px; border-radius: 50%; background: #000; }

/* Picker */
.cal-picker{
  position: absolute; top: 44px; left: 50%; transform: translateX(-50%);
  width: 260px; background: #fff; border: 1.4px solid #000; border-radius: 12px; padding: 10px;
  box-shadow: 0 10px 24px rgba(0,0,0,.18); z-index: 5;
}
.cal-picker-head{
  display: grid; grid-template-columns: 28px 1fr 28px 28px; gap: 6px; align-items: center; margin-bottom: 8px;
}
.cal-pnav, .cal-pclose{
  height: 28px; border: 1.2px solid #000; border-radius: 8px; background: #fff; cursor: pointer;
}
.cal-year-input{
  height: 28px; border: 1.2px solid #000; border-radius: 8px; padding: 0 8px; font-size: 13px;
}
.cal-picker-grid{
  display: grid; grid-template-columns: repeat(3, 1fr); gap: 6px;
}
.cal-pmonth{
  height: 36px; border: 1.2px solid #000; border-radius: 8px; background: #f7f7f7; font-size: 13px; font-weight: 700; cursor: pointer;
}
.cal-pmonth:hover{ background: #eaeaea; }

/* 관리자 잠금/해제 UI */
.cal-admin{
  display: flex; align-items: center; justify-content: space-between; gap: 6px; margin: 6px 0 8px;
}
.cal-lock{ display: grid; grid-template-columns: 1fr 56px; gap: 6px; }
.cal-pin{
  height: 32px; border: 1.2px solid #000; border-radius: 8px; padding: 0 8px; font-size: 12px;
}
.cal-unlock-btn, .cal-lock-btn{
  height: 32px; border-radius: 8px; border: 1.2px solid #000; background: #fff; font-size: 12px; font-weight: 800; cursor: pointer;
}
.cal-badge{
  display: inline-flex; align-items: center; justify-content: center;
  height: 22px; padding: 0 8px; font-size: 11px; border: 1px solid #000; border-radius: 999px; background: #00EAF5;
}
.cal-unlocked{ display: flex; align-items: center; gap: 8px; }

.cal-editor{ border-top: 1px solid #000; padding-top: 8px; }
.cal-editor-title{ font-size: 12px; margin-bottom: 6px; }
.cal-form{ display: grid; grid-template-columns: 1fr 80px 56px; gap: 6px; }
.cal-input{ height: 32px; border: 1.2px solid #000; border-radius: 8px; padding: 0 8px; font-size: 12px; }
.cal-add{ height: 32px; border-radius: 8px; border: 1.2px solid #000; background: #00EAF5; font-size: 12px; font-weight: 800; cursor: pointer; }
.cal-list{ list-style: none; margin: 8px 0 0; padding: 0; display: grid; gap: 6px; }
.cal-item{ display: grid; grid-template-columns: 8px 1fr auto; align-items: center; gap: 6px; }
.cal-bullet{ width: 5px; height: 5px; border-radius: 50%; background: #000; }
.cal-item-title{ font-size: 12px; }
.cal-del{ border: 1px solid #000; background: #fff; border-radius: 6px; height: 26px; padding: 0 8px; font-size: 12px; cursor: pointer; }

.cal-empty-msg{ color: #6b7280; font-size: 12px; }

/* 접근성 숨김 */
.sr-only{
  position: absolute !important; width: 1px; height: 1px; padding: 0; margin: -1px; overflow: hidden;
  clip: rect(0,0,0,0); border: 0;
}

/* ===== 레이아웃: 왼쪽 폼 + 오른쪽 사이드 ===== */
.contact .container.grid{
  display: grid;
  grid-template-columns: minmax(0, 1.15fr) 360px;
  gap: clamp(20px, 4vw, 40px);
  align-items: start;
}

/* 좌/우 영역 기본 */
.contact-main{ min-width: 0; }
.contact-aside{
  margin-left: auto;
  position: sticky; top: 24px;
  display: grid;
  gap: clamp(16px, 2.4vh, 28px);
}

/* 반응형: 모바일에서는 세로 스택 */
@media (max-width: 900px){
  .contact .container.grid{
    grid-template-columns: 1fr;
  }
  .contact-aside{
    position: static;
    margin-left: 0;
  }
}

/* Contact 섹션 높이 조정 */
.section--contact{
  min-height: 100svh;
  max-height: 100svh;
  align-content: start;
  overflow-y: auto;
}

/* 맨 아래 끝에 1px 버퍼: 바닥에서 끌려붙는 현상 방지 */
#pageRoot::after{
  content:"";
  display:block;
  height:1px;
}
